<template>
  <div id="message">
    <article class="title">
      <h2>留言板</h2>
      <p>请文明发言，禁止广告。不然拉黑了嗷\(^o^)/~</p>
      <el-input
        type="textarea"
        :rows="2"
        placeholder="请输入内容"
        maxlength="100"
        show-word-limit
        v-model="textarea">
      </el-input>
      <div class="btn">
        <i
          class="iconfont icon-biaoqing"
          @click.stop="handleToggleEmoji"
        ></i>
        <el-button type="primary" size="mini" @click="handleMsgBtn">发表</el-button>
        <div
          v-show="showEmoji"
          @click.stop
          class="emoji">
          <ul>
            <li
              v-for="(item,index) in emoji"
              @click="handleEmojiClick(item)"
              :key="index"
            >
              {{item}}
            </li>
          </ul>
        </div>
        <!-- 清除浮动 -->
        <div class="clear"></div>
      </div>
    </article>
    <article class="content">
      <!-- 留言部分 -->
      <ul>
        <li v-for="(item,index) in msgData" :key="index">
          <!-- 留言 -->
          <div class="parent">
            <div class="left">
              <img :src="urlBase + item.user.face" alt="">
            </div>
            <div class="right">
              <div class="name-time">
                <span class="name">{{item.user.user}}</span>
                <span class="time">{{item.time | msgTime}}</span>
              </div>
              <div class="message">{{item.msg}}</div>
              <div class="like-reply">
                <i class="like iconfont icon-xinaixin" :class="{isLikes: item.likes.indexOf(userInfo._id) !== -1}" @click="hangdleLikeClick(item,item)"></i>
                <span>{{item.likes.length}}</span>
                <!-- isLike: item.likes.indexOf(userInfo._id) !== -1 -->
                <i class="reply iconfont icon-huifu" @click="hangdleReplyClick(item,item)"></i>
              </div>
            </div>
          </div>
          <!-- 子留言 -->
          <div class="child" v-for="(childrenItem,childrenindex) in item.children" :key="childrenindex">
            <div class="left">
              <img :src="urlBase + childrenItem.user.face" alt="">
            </div>
            <div class="right">
              <div class="name-time">
                <span class="name">{{childrenItem.user.user}}</span>
                <span class="time">{{childrenItem.time | msgTime}}</span>
              </div>
              <div class="message">
                <span>@{{childrenItem.reply.user}}</span>
                {{childrenItem.msg}}
              </div>
              <div class="like-reply">
                <i class="like iconfont icon-xinaixin" :class="{isLikes:childrenItem.likes.indexOf(userInfo._id) !== -1}" @click="hangdleLikeClick(item,childrenItem,childrenindex)"></i>
                <span>{{childrenItem.likes.length}}</span>
                <i class="reply iconfont icon-huifu" @click="hangdleReplyClick(item,childrenItem)"></i>
              </div>
            </div>
          </div>
          <!-- 回复框 -->
          <div class="replybox" :class="{'show': item.isShowReply}">
            <el-input class="replyInput" v-model="item.replyText" :placeholder="'@' + item.replyUser.user" size="mini"></el-input>
            <el-button class="replyBtn" type="primary"  size="mini" @click="handleInputReply(item)">回复</el-button>
            <!-- 清除浮动 -->
            <div class="clear"></div>
          </div>
        </li>
      </ul>
    </article>
  </div>
</template>

<script>

// 引入vuex
import {mapState} from "vuex"

export default {
  naem: "Message",
  data(){
    return {
      textarea: "",
      showEmoji: false,
      emoji: [
        "😃", "😁", "😂", "😊", "🙃", "🥰", "😘", "🤭", "🤔", "😏",
        "😵", "😟", "🙁", "😳", "😰", "😭", "😖", "😡", "😠", "😈",
        "🤡", "☠️", "👻", "🙈", "🙉", "❤️", "💖", "💢", "💣", "💤",
        "👋", "👍", "👌", "🤞", "🤟", "🤙", "👊", "🖕", "🤝", "🙏",
        "🙅", "🙅‍♂️", "👨‍✈️", "👩‍✈️", "👷", "👷‍♀️", "🤦", "🤦‍♂️", "👴", "🧓",
        "🐴", "🐮", "🐖", "🐑", "🐓", "🦚", "🐲", "🐳", "🐟", "🐌",
      ],
      msgData: [],
        //  判断是否是开发环境  
        urlBase: process.env.NODE_ENV === "development"?"http://localhost:3000/":""

    }
  },
  computed: {
    ...mapState(["userInfo"])
  },
  // 过滤器
  filters: {
    // 对留言时间进行处理
    msgTime(value){
      let date = new Date(value);
      let YY = date.getFullYear();
      let MM = date.getMonth() + 1;
      let DD = date.getDate();
      let hh = date.getHours();
      let mm = date.getMinutes();
      let ss = date.getSeconds();


      // 双数字处理
      MM = (MM < 10 ? "0" : "") + MM;
      DD = (DD < 10 ? "0" : "") + DD;
      hh = (hh < 10 ? "0" : "") + hh;
      mm = (mm < 10 ? "0" : "") + mm;
      ss = (ss < 10 ? "0" : "") + ss;

      return `${YY}-${MM}-${DD}  ${hh}:${mm}:${ss}`
    }
  },
  methods:{
    //切换emoji的显示与隐藏
    handleToggleEmoji(){
      this.showEmoji = !this.showEmoji
    },

    //点击每一个emoji的时候，加入input
    handleEmojiClick(item){
      this.textarea += item
      this.showEmoji = false
    },

    // 请求留言数据
    async getMessageData(){
      let {data} = await this.$axios({
        method: "GET",
        url: "/msg/data",
      })
      data.data.filter(item =>{
        // 每条留言回复的内容
        item.replyText = ""
        // 记录回复那个用户
        item.replyUser = {...item.user}
        // 记录回复框显示或隐藏
        item.isShowReply = false

      })
      
      this.msgData = data.data

    },


    // 留言信息提交
    async handleMsgBtn(){
      // 检测用户是否登录
      let userId = this.userInfo._id
      if(!userId){
        return this.$message.error("请先登录，再留言！！！")
      }

      // 检测留言内容是否有数据
      let msg = this.textarea.trim();
      if(!msg){
        return this.$message.error("请先输入留言内容~")

      }
      // 向后端发送请求
      let {data} = await this.$axios({
        method: "POST",
        url: "/msg",
        data: {
          userId,
          msg
        }
      })

      // 验证留言信息
      if(data.code !== 0){
        return this.$message.error(data.msg)
      }
      //  验证成功
      this.$message({
        type: "success",
        message: data.msg,
        duration: 1000,
        onClose(){
          // 刷新页面
          window.location.reload()
        }
      })
    },


    // 点击liakes
    async hangdleLikeClick(item, thisItem,childrenindex){
      // 判断是否登录
        let userId = this.userInfo._id;
        if(!userId){
          return this.$message.error("请先登录")
        }

        // 判断是点赞还是取消赞
        let likes = thisItem.likes
        let index = likes.indexOf(userId)
        if(index !== -1){
          // 取消点赞
          likes.splice(index, 1)
        }else{
          // 点赞
          likes.push(userId)
        }

        // 将数据发送到数据库
        let {data} = await this.$axios({
          method: "POSt",
          url: "/msg/like",
          data: {
            parentId: item._id,
            childId: thisItem._id,
            likes,
            childrenindex
          }
        })
    },
    // 点击回复按钮弹出回复对话框
    async hangdleReplyClick(item,thisItem){
        if(thisItem.user._id === item.replyUser._id && item.isShowReply){
          item.isShowReply = false
          return
        }
        item.replyUser = thisItem.user
        item.isShowReply = true
        
    },
    //  回复对话框提交
    async handleInputReply(item){
      // 当前登录用户
        let user = this.userInfo._id;
        // 回复的谁
        let reply = item.replyUser._id;
        
        // 回复的内容
        let msg = item.replyText.trim();
        if(!user){
          return this.$message.error("请先登录")
        }
        if(!msg){
          return this.$message.error("请先输入留言内容")
        }
        // 楼主Id
        let msgId = item._id;
        // 留言信息

        let child = {msg, user, reply}
        let {data} = await this.$axios({
          method: "POST",
          url: "/msg/reply",
          data: {msgId, child}
        })
        console.log(reply)
        if(data.code !== 0){
          return this.$message.error(data.msg)
        }
        this.$message({
          type: "success",
          message: data.msg,
          duration: 1000,
          onClose(){
            window.location.reload()
          }
        })
    }
  },
  mounted() {
    // 点击任意地方，关闭emoji
    document.addEventListener("click",()=>{
      this.showEmoji = false
    })

    // 向后端请求留言数据
    this.getMessageData()
  }
}
</script>
<style lang="less" scoped>
#message {
  user-select: none;
  article.title {
    box-sizing: border-box;
    width: 100%;
    margin-bottom: 15px;
    box-shadow: 0 0 4px #ddd;
    padding: 25px 20px;
    background-color: #fff;
    font-family: "Quicksand", "Microsoft YaHei", sans-serif;
      h2 {
        margin-bottom: 15px;
        font-size: 20px;
        letter-spacing: 3px;
        line-height: 24px;
        border-left: 5px solid #73b899;
        text-indent: 10px;
      }
      p {
        text-indent: 2em;
        color: #444;
        font-size: 14px;
        letter-spacing: 4px;
        line-height: 30px;

        span {
          color: #bd4147;
          background-color: rgba(27, 31, 35, .05);
          font-weight: 700;
        }

        a {
          color: #409eff;

          &:hover {
            text-decoration: underline;
          }
        }
      }
        div.btn{
          position: relative;
          i{
            float: left;
            margin-top: 5px;
            font-size: 20px;
            cursor: pointer;
          }
          .el-button{
            float: right;
            margin-top: 5px;
          }
          .emoji{
            position: absolute;
            top: 30px;
            background-color: #fff;
            box-shadow: 0 0 3px #aaa;

            ul{
              width: 250px;
              height: 150px;
              margin: 8px;
              border-left: 1px solid #ddd;
              border-bottom: 1px solid #ddd;
              cursor: pointer;
              li{
                box-sizing: border-box;
                float: left;
                font-size: 18px;
                width: 25px;
                height: 25px;
                line-height: 22px;
                border-right: 1px solid #ddd;
                border-top: 1px solid #ddd;
              }
            }
          }
        }
  }


  article.content{
    background-color: #fff;
    ul{ 

        width: 970px;
        margin-left: 20px;
        padding-top: 15px;
        margin-right: 20px;
        li{
          padding-bottom: 8px;
          margin-bottom: 14px;
          font-size: 12px;
          border-bottom: 1px dashed #ddd;
          font-family: "Quicksand","Microspft YaHei","sans-serif";
          div.parent,div.child{
          display: flex;
          }
          div.child{
            margin-left: 55px;
            margin-top: 3px;
          }
          div.left{
            overflow: hidden;
            width: 40px;
            height: 40px;
            margin-right: 15px;
            border-radius: 50%;
            img{
              width: 100%;
              height: 100%;
            }
          }
          div.right{
            flex: 1;
            div.name-time{
              font-size: 12px;
              margin-bottom: 3px;
              span:nth-child(1){
                color: #73b899;
              }
              span:nth-child(2){
                color: #aaa;
                font-size: 12px;
                margin-left: 10px;
              }
              span.name{
                cursor: pointer;
              }
            }
            div.message{
              color: #666;
              letter-spacing: 3px;
              margin-bottom: 3px;
              word-break: break-word;
              span{
                color: #409eff;
                margin-right: 5px;
                cursor: pointer;
              }
            }
            div.like-reply{
              i{
                cursor: pointer;
                font-size: 14px;
                color: #aaa;

              }
              i.isLikes{
                color: rgb(255, 32, 32);
              }
              span{
                display: inline-block;
                font-size: 12px;
                width: 42px;
                margin-left: 2px;
                color: #aaa;
              }
            }
          }
          .replybox{
            overflow: hidden;
            margin-left: 55px;
            height: 0;
            box-sizing: border-box;
            transition: height .5s;
            &.show{
              height: 54px;
            }
            .replyInput{
              margin-bottom: 4px;
              width: 100%;
            }
            .replyBtn{
              padding: 4px 8px !important;
              float: right;
            }  
          }
        }

    }    
  }
  
} 


</style>